.stage {
  position: relative;
  min-height: 100vh;
  display: grid;
  place-items: center;
  overflow: hidden;
  background-color: #0f172a;
  color: #0f172a;
  isolation: isolate;
}

.carousel {
  position: absolute;
  inset: -4rem;
  z-index: 0;
}

.slide {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transform: scale(1.05);
  animation: slideCycle 30s infinite;
  animation-timing-function: ease-in-out;
  animation-delay: var(--delay, 0s);
  filter: saturate(110%) contrast(105%);
}

.slide:nth-child(1) {
  --delay: 0s;
  background-image:
    radial-gradient(circle at 15% 20%, rgba(94, 234, 212, 0.35), transparent 55%),
    radial-gradient(circle at 80% 15%, rgba(14, 165, 233, 0.3), transparent 45%),
    linear-gradient(135deg, #0f172a 0%, #1e293b 45%, #155e75 100%);
}

.slide:nth-child(2) {
  --delay: 6s;
  background-image:
    radial-gradient(circle at 20% 80%, rgba(251, 191, 36, 0.35), transparent 50%),
    radial-gradient(circle at 75% 30%, rgba(249, 115, 22, 0.3), transparent 55%),
    linear-gradient(145deg, #111827 0%, #1e293b 40%, #9a3412 100%);
}

.slide:nth-child(3) {
  --delay: 12s;
  background-image:
    radial-gradient(circle at 35% 30%, rgba(239, 68, 68, 0.35), transparent 55%),
    radial-gradient(circle at 70% 70%, rgba(244, 114, 182, 0.28), transparent 55%),
    linear-gradient(120deg, #0b1120 0%, #1f2937 35%, #831843 100%);
}

.slide:nth-child(4) {
  --delay: 18s;
  background-image:
    radial-gradient(circle at 25% 40%, rgba(96, 165, 250, 0.35), transparent 60%),
    radial-gradient(circle at 70% 60%, rgba(129, 140, 248, 0.35), transparent 55%),
    linear-gradient(155deg, #020617 0%, #1d4ed8 40%, #3730a3 100%);
}

.slide:nth-child(5) {
  --delay: 24s;
  background-image:
    radial-gradient(circle at 30% 70%, rgba(134, 239, 172, 0.32), transparent 55%),
    radial-gradient(circle at 70% 35%, rgba(59, 130, 246, 0.28), transparent 50%),
    linear-gradient(160deg, #020617 0%, #0f766e 50%, #10b981 100%);
}

.slide::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(120deg, rgba(2, 6, 23, 0.1) 0%, rgba(15, 23, 42, 0.4) 100%);
  mix-blend-mode: overlay;
}

.blurOverlay {
  position: absolute;
  inset: 0;
  z-index: 1;
  background: rgba(15, 23, 42, 0.18);
  animation: blurPulse 30s ease-in-out infinite;
  backdrop-filter: blur(0px);
}

.content {
  position: relative;
  z-index: 2;
  text-align: center;
  padding: clamp(3rem, 8vw, 6rem);
  display: grid;
  gap: 1.25rem;
}

.word {
  font-size: clamp(3rem, 10vw, 8rem);
  font-weight: 700;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: rgba(15, 23, 42, 0.35);
  text-shadow: 0 0 20px rgba(148, 163, 184, 0.25);
  animation: textReveal 30s ease-in-out infinite;
}

.subtitle {
  font-size: clamp(0.95rem, 2vw, 1.25rem);
  color: rgba(226, 232, 240, 0.75);
  max-width: 32ch;
  margin-inline: auto;
  line-height: 1.6;
}

@keyframes slideCycle {
  0%,
  16% {
    opacity: 1;
    filter: saturate(115%) contrast(110%) brightness(1);
  }
  20%,
  100% {
    opacity: 0;
    filter: saturate(100%) contrast(100%) brightness(0.8);
  }
}

@keyframes textReveal {
  0% {
    color: rgba(15, 23, 42, 0.3);
    text-shadow: 0 0 10px rgba(148, 163, 184, 0.2);
  }
  40% {
    color: rgba(15, 23, 42, 0.65);
    text-shadow: 0 0 30px rgba(226, 232, 240, 0.4);
  }
  100% {
    color: rgba(15, 23, 42, 0.95);
    text-shadow: 0 0 45px rgba(226, 232, 240, 0.55);
  }
}

@keyframes blurPulse {
  0% {
    backdrop-filter: blur(0px);
    background: rgba(15, 23, 42, 0.1);
  }
  35% {
    backdrop-filter: blur(16px);
    background: rgba(15, 23, 42, 0.22);
  }
  100% {
    backdrop-filter: blur(24px);
    background: rgba(15, 23, 42, 0.32);
  }
}

@media (max-width: 768px) {
  .stage {
    min-height: 80vh;
  }
  .carousel {
    inset: -2rem;
  }
}
